import { createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

export const fetchUserData = createAsyncThunk(
  'user/fetchUserData',
  async (payload, { rejectWithValue }) => {
    try {
      const response = await axios.get('/user');
      return response.data;
    } catch (error) {
      // Obsuga rnych typw bdw
      if (error.response) {
        // Serwer odpowiedzia z kodem bdu
        return rejectWithValue(error.response.data);
      } else if (error.request) {
        // Zapytanie zostao wysane, ale brak odpowiedzi
        return rejectWithValue('No response from the server');
      } else {
        // Co poszo le przy konfiguracji zapytania
        return rejectWithValue(error.message);
      }
    }
  }
);

export const setUserBalance = createAsyncThunk(
  'user/setUserBalance',
  async (payload, { rejectWithValue }) => {
    try {
      const response = await axios.patch('/user/balance', { balance: payload });
      return response.data;
    } catch (error) {
      // Obsuga rnych typw bdw
      if (error.response) {
        // Serwer odpowiedzia z kodem bdu
        return rejectWithValue(error.response.data);
      } else if (error.request) {
        // Zapytanie zostao wysane, ale brak odpowiedzi
        return rejectWithValue('No response from the server');
      } else {
        // Co poszo le przy konfiguracji zapytania
        return rejectWithValue(error.message);
      }
    }
  }
);
